import { MessageBox } from 'mint-ui'
export default {
      name: "mobileHead",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          isLogin:false,
          moreList:[
            {img: '../../../static/img/static/wap/home.png',title: '',router:'/index'},
            {img: '../../../static/img/static/wap/search.png',title: '',router:'/mSearch'},
            {img: '../../../static/img/static/wap/ower.png',title: '',router:'/meditOwer'},
            {img: '../../../static/img/static/wap/equit.png',title: ''},
          ],
          isLook:false
        }
    },
  components:{
    MessageBox,
  },
    methods:{
        getToken(){
          if(localStorage.getItem('token')){
              this.$http.post(this.api + '/home/User/get_user',{
                token: localStorage.getItem('token')
              })
                .then(res=>{
                  if(res.data.code == 1){
                    this.isLogin = true
                    localStorage.setItem('isLogin',true)
                    this.$emit('mobileReload',res)
                  }else if(res.data.code == 3){
                    this.$http.post(this.api + '/home/Index/token')
                      .then(res=>{
                        localStorage.setItem('token',res.data.data)
                        this.$emit('mobileReload')
                        localStorage.removeItem('isLogin')
                      })
                  }else{
                    localStorage.removeItem('isLogin')
                    this.$emit('mobileReload')
                  }
                })
          }else{
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.$emit('mobileReload')
              })
          }
        },
      toLogin(){
          this.$router.push('/mobileLogin')
      },
      lookMore(){
          this.isLook = !this.isLook
      },
      toOther(item,index){
          if(index == 3){
            this.quit()
          }else{
            this.$router.push(item.router)
          }

    },
      quit(){
          this.$http.post(this.api + '/home/User/quit',{
            token: localStorage.getItem('token')
          })
            .then(res=>{
              if(res.data.code == 1){
                this.$router.push('/index')
                localStorage.removeItem('isLogin')
                localStorage.removeItem('token')
                this.isLogin = false
                this.isLook = false
                this.getToken()
                MessageBox.alert('')
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.quit()
                  })
              }else if(res.data.code == 0){
                MessageBox.alert(res.data.msg)
              }
            })
      },
      checkApp(){
        var userAgent = navigator.userAgent ;
        var url="",downUrl="";
        if(userAgent.indexOf("Android")>-1){
          /*url='weixin://';//App
          downUrl="XXXXXXXXX"; //App*/
          MessageBox.alert('APP')
        }else{
          url='https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1';//IOSAppIOS
          downUrl="https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1"; //IOSAppIOS
        }
        function test(){
          window.location.href = url;
          setTimeout(function(){
            window.location.href = downUrl;
          },5000)
        }
        test();
      },
      openApp(){
        var userAgent = navigator.userAgent ;
        var url="",downUrl="";
        if(userAgent.indexOf("Android")>-1){
          url='/.apk';//App
          downUrl='/.apk'; //App
        }else{
          url='https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1';//IOSAppIOS
          downUrl="https://apps.apple.com/us/app/%E4%BA%BA%E4%BA%BA%E8%80%8D/id1482092521?l=zh&ls=1"; //IOSAppIOS
        }
        window.open(downUrl)
      }
    },
    mounted() {
        this.getToken()
    },
    created(){
        if(localStorage.getItem('isLogin')){
          this.isLogin = localStorage.getItem('isLogin')
        }
    }
  }



